@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,400;1,600;1,700&family=Public+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 20px 20px;
    background-color: hsl(0, 0%, 98%);
    position: fixed;
    top: 0;
    width: 100%;
    z-index:  1000;
    grid-gap: 7em;
    gap: 7em;
}

.nav-content {
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    align-items: center;
    grid-gap: 2em;
    gap: 2em;
    cursor: pointer;
    color: #808080;
}

.nav-content  li:hover::after  {
  content: '';
  background: linear-gradient(to right, hsl(136, 65%, 51%),  hsl(192, 70%, 51%));;
  display: flex;
  position: absolute;
  bottom: 0;
  height: 5px;
}

.nav-content .home::after {
  width: 38px;
}

.nav-content .about::after {
  width: 38px;
}

.nav-content .contact::after {
  width: 50px;
}

.nav-content .blog::after {
  width: 30px;
}

.nav-content .careers::after {
  width: 50px;
}

.nav-btn {
    border-radius: 25px;
    border: 1px solid #808080;
    color: #fff;
    background:  linear-gradient(to right, hsl(136, 65%, 51%),  hsl(192, 70%, 51%));
    padding: 15px 40px;
    cursor: pointer;
}

.nav-btn:hover {
   filter: opacity(0.7);
}


.hamburger {
    cursor: pointer;
    height: 15px;
    width: 30px;
    display: none;
    background-image: url(/EasyBank/static/media/icon-hamburger.b0d311fe.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.hamburger.toggle {
  background-image: url(/EasyBank/static/media/icon-close.c2e36e04.svg);
  cursor: pointer;
  height: 20px;
  width: 30px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.mobile {
  display: none;
}

@media (max-width: 768px) {
    .Nav {
      grid-gap: 2em;
      gap: 2em;
    }

    .nav-content {
      grid-gap: 1em;
      gap: 1em;
    }

}


@media (max-width: 425px) {
  .hamburger {
    display: block;
  }

  .nav-content {
    display: none;
  }

  .Nav {
      justify-content: space-between;
      padding: 20px 40px;
  }
  .nav-btn {
    display: none;
  }

  .mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: #fff;
    top: -300px;
    right: 50px;
    left: 50px;
    padding: 60px 40px;
    border-radius: 8px;
    transition: all ease-in-out 350ms;
  }

  .mobile.nav {
    top: 65px;
    transition: all ease-in-out 350ms;
  }

  .mobile-list {
    list-style: none;
    text-align: center;
  }

  .mobile-list li {
    padding-bottom: 20px;
  }

  .mobile-list li:hover {
    color:  hsl(136, 65%, 51%);
  }
}

.intro {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 120px;
    position: relative;
    background-image: url(/EasyBank/static/media/bg-intro-desktop.8be308fd.svg);
    margin-top: 5em;
    background-repeat: no-repeat;
    background-position: right -33em bottom -13em;
    background-size: cover;
    height: 95vh;
    position: relative;
    overflow-x: clip;
}



.intro img {
    position: absolute;
    right: -130px;
    top: -120px;
}


.intro h1 {
    padding-bottom: 10px;
    font-size: 48px;
    font-weight: 400;
}

.intro-lap {
    padding-top: 15px;
    padding-bottom: 20px;
    line-height: 1.5em;
}

.para-mobile {
    display: none;
}

.intro-btn {
    border-radius: 25px;
    border: 1px solid #808080;
    color: #fff;
    background:  linear-gradient(to right, hsl(136, 65%, 51%),  hsl(192, 70%, 51%));
    padding: 15px 40px;
    cursor: pointer;
}

.intro-btn:hover {
   filter: opacity(0.7);
}

@media (max-width: 1024px) {
    .intro {
        padding: 30px 40px;
        height: 80vh;
        background-position: right -28em bottom -9.5em;
    }

    .intro > div {
        margin-top: 4em;
    }
    .intro img {
        transform: scale(.7);
        top: -215px;
        right: -206px;
    }
}

@media (max-width: 768px) {

    .intro {
    background-position: right -23em bottom -8.5em;
    height: 55vh;
    }

   
    .intro img {
        transform: scale(.5);
        right: -260px;
        top: -285px;
    }

    .intro > div {
        padding-left: 0px;
        margin-top: 3em;
    }

    .intro-lap {
        font-size: small;
    }
}

@media (max-width: 425px) {
    .intro {
        height: auto;
        height: initial;
        align-items: unset;
        flex-direction: column-reverse;
        margin-top: -6em;
        background-image: url(/EasyBank/static/media/bg-intro-mobile.c2ed38d0.svg);
        background-position: right 0em bottom 17em;
        background-size: contain;
    }

    .intro h1 {
        font-size: 46px;
    }

    .intro > div {
        padding-left: 0;
        margin-top: 3em;
        text-align: center;
    }

    .intro img {
        transform: none;
        position: static;
        position: initial;
    }

    .intro-lap {
        display: none;
    }

    .para-mobile {
        display: block;
        line-height: 1.5em;
    }

    .intro-btn {
        margin-top: 2em;
    }
}

@media (max-width: 375px) {
    .intro {
        background-position: right 0em bottom 20.5em;
        margin-top: -5em;
    }

    .intro h1 {
        font-size: 37px;
    }

    .intro-lap {
        display: none;
    }

    .para-mobile {
        padding-top: 40px;
        margin: -1em;
        font-size: 15px;
    }
}

@media (max-width: 320px) {
    .intro {
        background-position: right 0em bottom 18.3em;
        margin-top: -4em;
    }

    .intro h1 {
        font-size: 32px;
    }

    .para-mobile {
        font-size: small;
    }
}
.reason-container {
    margin-top: 2em;
    display: flex;
    align-items: center;
    grid-gap: 1.4em;
    gap: 1.4em;
}

h2 {
    padding-top: 2em;
    font-weight: 400;
}

@media (max-width: 1024px) {
    .reason-container {
        grid-gap: .5em;
        gap: .5em;
    }

}

@media (max-width: 768px) {
    h2 {
        font-size: 18px;
    }
}

@media  (max-width: 425px) {
    .reason-container {
        flex-direction: column;
        text-align: center;
    }

    h2 {
        font-size: revert;
    }
}
.why-space {
    padding: 160px 120px;
    background: hsl(0, 0%, 98%);
}

.why-space p {
    padding-top: 2em;
    padding-bottom: 2em;
    line-height: 1.5em;
}

.why-space h1 {
    font-weight: 400;
    font-size: 37px;
}

@media (max-width: 1024px) {
    .why-space {
        padding: 115px 40px;
    }
}

@media (max-width: 1024px) {
    .why-space p {
        font-size: x-small;
    }
}

@media (max-width: 425px) {
    .why-space > div {
        text-align: center;
    }

    .why-space p {
        font-size: medium;
        font-size: initial;
    }
}

@media (max-width: 320px) {
    .why-space h1 {
        font: 28px;
    }

    .why-space p {
        font-size: small;
    }
}
.article {
    height: 30vh;
}

.article img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.article div {
    background: hsl(0, 0%, 98%);
    margin-top: -5px;
    padding: 15px 37px 15px 37px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.article h6 {
    padding-top: 30px;
    padding-bottom: 10px;
    font-weight: 400;
}

.article h4 {
    font-weight: 400;
    padding-bottom: 1em;
}

.article h4:hover {
    color: hsl(136, 65%, 51%);
    cursor: pointer;
}

.article p {
    font-size: 12px;
    line-height: 1.5em;
}

@media (max-width: 1024px) {
    .article div {
        padding: 10px 20px;
    }

    .article h4 {
        padding-bottom: .6em;
    }
}

@media (max-width: 768px) {
    .article div {
        padding: 10px 11px;
    }

    .article {
        height: 25vh;
    }

    .article h4 {
        font-size: x-small;
    }

    .article p {
        font-size: xx-small;
    }
}

@media (max-width: 425px) {
    .article {
        height: revert;
    }

    .article div {
        padding: 30px 49px;
    }

    .article {
        height: auto;
        height: initial;
    }

    .article h4 {
        font-size: revert;
    }

    .article p {
        font-size: 12px;
    }
}

@media (max-width: 375px) {
    .article div {
        padding: 30px 24px;
    }
}

@media (max-width: 320px) {
    .article div {
        padding: 30px 20px;
    }
}
.latest {
    padding: 160px 120px;
}

.latest h1 {
    font-weight: 400;
}

.latest-container {
    margin-top: 4em;
    margin-bottom: 4em;
    display: flex;
    align-items: center;
    grid-gap: 1.5em;
    gap: 1.5em;
}

@media (max-width: 1024px) {
    .latest {
        padding: 115px 40px;
    }

    .latest-container {
        grid-gap: 1em;
        gap: 1em;
    }
}

@media (max-width: 425px) {
    .latest-container {
        flex-direction: column;
    }

    .latest h1 {
        text-align: center;
    }

}
.footer {
  display: grid;
  margin-top: 3em;
  padding: 40px 120px;
  grid-template-columns: auto auto;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  background:  hsl(233, 26%, 24%);
}


.footer-nav {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  grid-gap: 8em;
  gap: 8em;
}

.footer-logo {
  color: #fff;
}

.footer-copy-right {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  justify-content: center;
  grid-gap: 1.5em;
  gap: 1.5em;
  color:  hsl(0, 0%, 98%);
}

.footer-btn {
  border-radius: 25px;
  border: 1px solid #808080;
  color: #fff;
  background:  linear-gradient(to right, hsl(136, 65%, 51%),  hsl(192, 70%, 51%));
  padding: 15px 40px;
  cursor: pointer;
}

.footer-btn:hover {
 filter: opacity(0.7);
}

.nav-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  grid-gap: .7em;
  gap: .7em;
}

.nav-list li {
  cursor: pointer;
}

.nav-list li:hover {
  color: hsl(136, 65%, 51%);
}

.media-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
}

.media {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  grid-gap: 1em;
  gap: 1em;
}

.media svg:hover {
  color: hsl(136, 65%, 51%);
  cursor: pointer;
  fill: currentColor;
}

@media (max-width:1024px) {
  .footer {
    padding: 30px 40px;
  }

  .footer-nav {
    grid-gap: 5em;
    gap: 5em;
  }
}

@media (max-width:768px) {
  .footer-nav {
    grid-gap: 3em;
    gap: 3em;
  }

  .footer {
    font-size: small;
  }
}

@media (max-width: 425px) {
  .footer {
    display: flex;
    flex-direction: column;
    font-size: medium;
    font-size: initial;
  }

  .footer-nav {
    display: flex;
    flex-direction: column;
    margin-bottom: 3em;
  }

  button.btn-invite {
    display: block;
  }

  .nav-list {
    align-items: center;
  }
  
  .footer-copy-right {
    align-items: center;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow Semi Condensed', sans-serif;
  font-family: 'Public Sans', sans-serif
}

h1,
h2 {
  font-family:  'Public Sans', sans-serif;
  color: hsl(233, 26%, 24%);
}

p,
small,
h6 {
  color: #808080;
}



